.contact {
  background-color: $contact-color;
  &__blocks {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    text-align: left;
    &-name {
      margin-top: 40px;
      @include font-light;
      font-size: 30px;
      width: 100%; } }
  &__data {
    position: relative;
    width: 49%;
    @include font-regular;
    font-size: 16px; }
  &__header {
    margin: 20px 0 15px;
    @include font-bold;
    font-size: 20px;
    opacity: 0.8; }
  .address {
    margin-bottom: 75px; }
  &-mark {
    opacity: 0.8; }
  &__map {
    position: absolute;
    top: 94px;
    left: 270px;
    width: 260px;
    height: 220px;
    border-radius: 5px;
    overflow: hidden; }
  &__form {
    width: 49%; }
  .form {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    &__field {
      margin: 20px 0 0;
      padding: 13px 18px;
      background-color: darken(desaturate($contact-color, 6.81), 6.47);
      outline: none;
      border: none;
      border-radius: 5px;
      &::placeholder {
        color: $contact-color; } }
    input {
      width: 48.1%; }
    textarea {
      width: 100%;
      height: 155px; }
    &__btn {
      display: block;
      margin: 20px auto 0;
      padding: 10px 90px;
      @include font-black;
      font-size: 26px;
      text-transform: uppercase;
      color: $text-color;
      background-color: lighten(saturate($services-color, 25.91), 12.35);
      box-shadow: 0 5px lighten(saturate($services-color, 25.91), 2.35);
      border: none;
      outline: none;
      border-radius: 5px;
      &:hover {
        cursor: pointer;
        box-shadow: 0 5px lighten(saturate($services-color, 25.91), 2.35), 0 0 10px lighten(saturate($services-color, 25.91), 12.35); } } } }

@media (max-width: 1200px) {
  .contact {
    &__blocks {
      &-name {
        text-align: center; } }
    &__data {
      width: 100%;
      max-width: 550px;
      margin-left: auto;
      margin-right: auto; }
    &__form {
      width: 100%;
      max-width: 700px;
      margin-left: auto;
      margin-right: auto; } } }


@media (max-width: 992px) {
  .contact {
    .form {
      input {
        width: 100%; } } } }
